import React, {Component} from 'react';
import axios from 'axios';
import update from 'immutability-helper';
import Preloader from '../preloader';
import {BrowserRouter as Router, Route} from 'react-router-dom';

import TodoList from '../todo-list';



export default class App extends Component {
  state = {
    todoData: [],
    load: false
  };

  //Get request to API
  componentDidMount() {
    return axios.get(`https://secure-springs-77898.herokuapp.com/api/tasks/active/`)
      .then(res => {
        const tasks = res.data;
        this.setState({todoData: tasks, load: true});
        return res.data.ok;
      });
  }

   // Update status after name was changed
  updateState = (idChange, name) => {
    console.log(idChange, name);
    const i = this.state.todoData.findIndex((item, i) => {

      return (item.id === idChange);
    });
    this.setState({
      todoData: update(this.state.todoData, {[i]: {name: {$set: name}}})
    })
  };

  render() {
    const {todoData} = this.state;
    if (this.state.load) {
      return (
        <Router>

          <div className="todo-app">
            <Route exact path="/" component={() => <TodoList todos={todoData}/>}/>
            <Route exact
                   path="/:id"
                   render={({match}) => {
                     const idTask = match.params.id;
                     const singleTask = todoData.find(item => item.id === parseInt(idTask));
                     return (
                       <TodoList
                         todos={singleTask}
                         itemId={idTask}
                         handleFocusOut={ this.handleFocusOut }
                         updateState={this.updateState} />
                     );
                   }} />
          </div>
        </Router>
      )
    } else {
      return  <Preloader/>;
    }
  }
}