import React, {Component} from 'react';
import EditableLabel from 'react-inline-editing';
import axios from 'axios';
import {Link} from 'react-router-dom';


export default class TodoListItem extends Component {

  //PUT request for API after change task name and focus out
  handleFocusOut = (text, id) => {
    this.props.updateState(this.props.id, text);

    return axios.put(
      `https://secure-springs-77898.herokuapp.com/tasks/${this.props.id}/`,
      {id: this.props.id, name: text})
      .then(res => {
        if (res.data.ok && res.data.nModified) {
          this.props.updateState(this.props.id, text);
          console.log('Operation success');
        }
      });
  };

  render() {
    const task = this.props;
    let dueDate = task.due_date;
    let classBold = '';
    if (task.is_high_priority) {
      classBold = ' bold';
    }
    if (task.due_date) {
      dueDate = new Date(task.due_date).toDateString();
    } else {
      dueDate = '';
    }

    let taskStags;
    if (task.tags) {
      taskStags = task.tags.map((item, i) => <span key={i} className="tags">#{item}</span>);
    }
    return (
      <React.Fragment>
        <td>
          <Link to={`/${this.props.id}`} className={`tasks-list__name${classBold}`}>
            <EditableLabel
              text={task.name}
              onFocusOut={this.handleFocusOut}
            />
          </Link>
          {taskStags}
        </td>
        <td><span className="tasks-list__dueDate">{dueDate} </span></td>
        <td><span className="tasks-list__estimated-effort">{task.estimated_effort || ''}</span></td>
        <td><span className="tasks-list__actual_effort">{task.actual_effort}</span></td>
      </React.Fragment>
    );
  }
}

