import React from 'react';
import {Link} from 'react-router-dom';
import TodoListItem from '../todo-list-item';

const TodoList = ({todos, itemId, handleFocusOut, updateState}) => {
  let elements;
  const tableHeader = <tr>
    <th>Name</th>
    <th>End date</th>
    <th>Rating</th>
    <th>Spent time</th>
  </tr>;

  if (itemId) {
    const {...itemProps} = todos;
    return (
      <div className="todo">
        <button className="task-list__home-button">
          <Link to={'/'}>Home</Link>
        </button>
        <table className="task-list">

          <tbody>
          {tableHeader}
          <tr key={todos.id}>
            <TodoListItem
              {...itemProps}
              itemPage={true}
              handleFocusOut={handleFocusOut}
              updateState={updateState}/>
          </tr>
          <tr><td id="tasks-list__description" colSpan={4}>{todos.description}</td></tr>
          </tbody>
        </table>
      </div>
    );
  } else {
    elements = todos.map((item) => {
      const {...itemProps} = item;
      return (
        <tr key={item.id}>
          <TodoListItem
            {...itemProps}
          />
        </tr>
      );
    });
  }

  return (
    <table className="task-list">
      <caption className="task-list__caption">Todo list</caption>
      <tbody>
      {tableHeader}
      {elements}
      </tbody>
    </table>
  );
};

export default TodoList;

